.header {
    display: flex;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    align-items: center;
    /* padding: 0; */
    color: white;
    background-color: black;
    z-index: 10000;
    box-shadow: 0 1px 5px rgba(0, 0, 0, 0.25);
    transition: all 0.2s ease-in-out;
    height: auto;
    
}
.header__left{
    flex: 0.4 1;
}

.header__left > h1{
    text-align: center;
    font-family: "Padauk", sans-serif;
    letter-spacing: 1px;
    padding: 16px 0;
}

.header__left > h1 > span{
    color: #e74c3c;
}

.header__right{
    display: flex;
    flex: 0.6 1;
    justify-content: space-evenly;
    text-align: center;
}

.header__right > a > h4{
    font-size: 18px;
    cursor: pointer;
    transition: transform 200ms ease-in-out;
    font-family: "Crimson Text", serif;
}

.header__right > a > h4:hover{
    transform: scale(1.09);
    background-color:#e74c3c;
    color: white;
    padding: 3px 13px;
    border-radius: 2px;
    letter-spacing: 1px;
    font-family: "Crimson Text", serif;
    font-size: 15px;
    cursor: pointer;
}

.header__rightbutton{
    font-size: 16px;
    background-color: #e74c3c;
    color: white;
    padding: 3px 13px;
    border-radius: 2px;
    cursor: pointer;
}
